@mixin rich-media-hero__specs {
  @include perspective(800px, horizontal, '.text');
  //transform: translateY(24.2em) translateX(1.4%);

  .text {
    @include p;
    @include text-style(headline-3);
    text-align: center;

    @include rich-media-hero--resize($lem * 2);
    @include border(thin, all);
    padding: $lem;
    box-sizing: border-box;
  }

  @include rich-media-hero__specs--mobile;
  @include rich-media-hero__specs--tablet;
}
